<template>
  <div class="containerAbout">
    <div class="swiper-wrapper">
      <!--技术驱动-->
      <div class="swiper-slide">
        <div class="aboutPage_1">
          <div class="page">
            <transition name="pageSlide_1">
              <div  v-show="slideControl.pageSlide_1">
                <img class="title"  src="./01_gywm_zcwz.png" />
                <img class="right" src="./01_gywm_ycwz.png" />
              </div>
            </transition>
          </div>
        </div>
      </div>
      <!--我们-->
      <div class="swiper-slide">
        <div class="aboutPage_2">
          <div class="page">
            <transition name="pageSlide_2">
              <div  v-show="slideControl.pageSlide_2">
                <img class="title"  src="./02_gywm_zcwz.png" />
                <img class="right" src="./02_gywm_ycwz.png" />
              </div>
            </transition>
          </div>
        </div>
        <rjFooter></rjFooter>
      </div>
    </div>
    <!--分页-->
    <div class="swiper-pagination" ></div>
  </div>
</template>
<script type="text/ecmascript-6">
  import rjFooter from '../footer/footer.vue'
  export default {
    data(){
      return {
        slideControl:{
          pageSlide_1:false,
          pageSlide_2:false,
        }
      }
    },
    mounted(){
      var that = this,target = that.slideControl;
      this.swiper = new Swiper('.containerAbout',{
        direction:'vertical',
        pagination:'.swiper-pagination',
        paginationClickable:true,
        mousewheelControl:true,
        onInit: function(swiper){ //Swiper2.x的初始化是onFirstInit
          that.slideControl. pageSlide_1 = true;
        },
        onSlideChangeStart:function(swiper){
          var index = swiper.activeIndex;
          index == 0? target. pageSlide_1 = true : target.pageSlide_1 = false;
          index == 1?  target. pageSlide_2 = true : target.pageSlide_2 = false;
        },
      });
    },
    components:{rjFooter}
  }
</script>
<style scoped lang="stylus">
  @import "../../common/stylus/mixins.styl"
  .containerAbout
    width 100%
    height 100%
    overflow hidden
    .swiper-slide
      width 100%
      height 100%
      .aboutPage_1   //技术驱动
        $screen-1080-bg("./01_gywm_background.png","./gywm_01_bg_1.png")
        $bg-size cover
        background-position center
        $table-box()
        $transition-title('pageSlide_1')
        .page
          $table-cell()
          img
            vertical-align middle
            @media only screen and (max-width: 768px)
              width 80%
            &.title
              @media only screen and (max-width: 768px)
                width 50%
            &.right
              margin-left 245px
              @media only screen and (max-width: 1080px)
                margin-left 0
              @media only screen and (max-width: 768px)
                margin-top 20px

      .aboutPage_2   //我们
        $screen-1080-bg("./02_gywm_background.png","./gywm_02_bg_2.png")
        $bg-size cover
        background-position center
        $table-box()
        $transition-title('pageSlide_2')
        .page
          $table-cell()
          img
            vertical-align top
            @media only screen and (max-width: 768px)
              width 80%
            &.title
              @media only screen and (max-width: 768px)
                width 50%
            &.right
              vertical-align middle
              margin-left 66px
              @media only screen and (max-width: 1080px)
                margin-left 0
                margin-top 20px
              @media only screen and (max-width: 768px)
                margin-top 20px






  .swiper-wrapper>div
      @media only screen and (min-width: 1080px)
        min-height 700px
</style>




