<template>
  <div class="containerServer">
    <div class="swiper-wrapper">
      <!--个人信贷系统-->
      <div class="swiper-slide creditHomeWrap">
        <transition name="creditSlide">
          <div class="title" v-show="slideControl.creditSlide">
            <img src="./01_cpfw_wz.png" alt="">
          </div>
        </transition>
      </div>
      <!-- 个人信贷 -->
      <div class="swiper-slide rjCreditWrap">
        <transition name="rjCreditSlide">
          <div class="rjTitle" v-show="slideControl.rjCreditSlide">
            <img src="./02_cpfw_wz.png" alt="">
          </div>
        </transition>
        <div class="rjCreditWrap-detail">
          <div class="images"><img src="./02_cpfw_zc.png" alt=""></div>
          <transition name="rjDetail">
            <div class="context" v-show="slideControl.rjCreditContext">
              <p>融钜个人信贷系统是融钜针对中小金融机构新型小贷业务需求，研发的一款无抵押、无担保、纯线上小额贷款产品。</p>
              <p>该系统基于大数据分析，结合风控模型对借款人资信进行评估，实现秒级快速放款。还可根据不同产品设置不同的还款方式、费用标准、风控模型和审批流程。</p>
              <p>改产品为年轻消费群体提供高效、便捷、个性化的信用借款服务。</p>
            </div>
          </transition>
        </div>
      </div>
      <!--中小机构-->
      <div class="swiper-slide outfitWrap">
        <div class="outfit-list">
          <transition name="outfitSlide">
            <div class="outfitTitle" v-show="slideControl.outfitSlide">
              <img src="./03_cpfw_wz.png" alt="">
            </div>
          </transition>
          <div class="outfit-img">
            <ul class="leftWrap">
              <li class="leftTop"><img src="./03_cpfw_td1.png" alt="">
                <transition>
                  <p>缺乏专业风险管理技术</p>
                  <!--<p>缺乏专业风险管理技术</p>-->
                </transition>

              </li>
              <li class="leftBottom"><img src="./03_cpfw_td4.png" alt="">
                <p>金融业务人员少，效率低</p>
              </li>
            </ul>
            <ul class="middleWrap">
              <li class="middle"><img src="./03_cpfw_td5.png" alt="">
                <p>信贷产品设计缺少或单一</p>
              </li>
            </ul>
            <ul class="rightWrap">
              <li class="rightTop"><img src="./03_cpfw_td2.png" alt="">
                <p>缺少大数据建模和分析能力</p>
              </li>

              <li class="rightBottom" ><img ref="rightBottom" src="./03_cpfw_td3.png" alt="">
                <p>金融IT实力薄弱</p>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <!--业务模式-->
      <div class="swiper-slide cooperationPartner">
         <div class="partner-list" >
           <transition name="partnerSlide">
             <div class="title" v-show="slideControl.partner">
               <img src="./cpfw-04-wz.png" alt="">
             </div>
           </transition>
           <div class="partner">
             <img src="./jxkj.png" alt="">
           </div>
         </div>
      </div>
      <!--架构-->
      <div class="swiper-slide frameworkWrap">
        <div class="framework-list" >
          <transition name="frameworkSlide">
            <div class="title" v-show="slideControl.framework">
              <img src="./cpfw-05-wz.png" alt="">
            </div>
          </transition>
          <div class="partner">
            <img src="./05_cpfw_jgt.png" alt="">
          </div>
        </div>
      </div>
      <!--完美产品服务-->
      <div class="swiper-slide perfectProWrap">
        <div class="perfect-list" >
          <transition name="perfectSlide">
            <div class="title" v-show="slideControl.perfect">
              <img src="./cpfw-06-wz.png" alt="">
            </div>
          </transition>
          <ul class="perfect">
            <li><img src="./06_cpfw_1_rzgl.png" alt=""></li>
            <li><img src="./06_cpfw_2_sjzq.png" alt=""></li>
            <li><img src="./06_cpfw_3_sxsh.png" alt=""></li>
            <li><img src="./06_cpfw_4_jkqk.png" alt=""></li>
            <li><img src="./06_cpfw_5_hk.png" alt=""></li>
            <li><img src="./06_cpfw_6_dxtx.png" alt=""></li>
          </ul>
        </div>
        <rjFooter></rjFooter>
      </div>
    </div>
    <!--分页-->
    <div class="swiper-pagination" ></div>
  </div>
</template>
<script type="text/ecmascript-6">
  import rjFooter from '../footer/footer.vue'
  export default {
    data(){
      return {
       slideControl:{
         creditSlide:false,
         rjCreditSlide:false,
         rjCreditContext:false,
         outfitSlide:false,
         partner:false,
         framework:false,
         perfect:false
       }
      }
    },
    mounted(){
      var that = this,target = that.slideControl;
      this.swiper = new Swiper('.containerServer',{
        direction:'vertical',
        pagination:'.swiper-pagination',
        paginationClickable:true,
        mousewheelControl:true,
        onInit: function(swiper){ //Swiper2.x的初始化是onFirstInit
          that.slideControl. creditSlide = true;
        },
        onSlideChangeStart:function(swiper){
          var index = swiper.activeIndex;
          index == 0? target. creditSlide = true : target.creditSlide = false;
          index == 1? (target.rjCreditSlide = true,target.rjCreditContext = true) :
            (target.rjCreditSlide = false,target. rjCreditContext = false);
          index == 2?  target. outfitSlide = true : target.outfitSlide = false;
          index == 3?  target. partner = true : target.partner = false;
          index == 4?  target. framework = true : target.framework = false;
          index == 5?  target. perfect = true : target.perfect = false;
          if (index ==2){
            console.log( that.$refs.rightBottom.style.width)
          }
        },
      });
//      this.$nextTick(function () {
//        console.log( this.$refs.rightBottom.style.width)
//      });
      console.log( this.$refs.rightBottom.style.width)
      this.$refs.rightBottom.style.right = - this.$refs.rightBottom.style.width+'px';
    },
    components:{rjFooter}
  }
</script>
<style scoped lang="stylus">
  @import "../../common/stylus/mixins.styl"
.containerServer
  width 100%
  height 100%
  overflow hidden
  .creditHomeWrap   //个人信贷系统
    $table-box()
    $screen-1080-bg("./01_cpfw_background.png","./01_cpfw_bg_small.png")
    $bg-size cover
    background-position center
    .title
      $table-cell()
      img
        height auto
        $screen-1080-img-width()
    $transition-title('creditSlide')
  .rjCreditWrap   //个人信贷
    $screen-1080-bg("./02_cpfw_background.png","./02_cpfw_bg_small.png")
    $bg-size cover
    background-position center
    $transition-title('rjCreditSlide')
    .rjTitle
      text-align center
      margin-top  150px
      $screen-1080-margin-top()
      img
        height auto
        $screen-1080-img-width()
    .rjCreditWrap-detail
      position absolute
      width 100%
      left 50%
      top 60%
      @media only screen and (max-width: 1080px)
        top 60%
      $translate(-50%,-50%)
      text-align center
      margin 0 auto
      font-size 0
      div
        display inline-block
        @media only screen and (max-width: 1080px)
          display block
        font-size 16px
        color #676767
        text-align start
        vertical-align middle
      $transition-title('rjDetail')
      .images
        text-align center
        img
          @media only screen and (max-width: 1080px)
            width 80%
      .context
        width 25%
        p
          line-height 24px
        @media only screen and (max-width: 1080px)
          margin 0 auto
          width 90%



  .outfitWrap   //中小机构
    $screen-1080-bg("./03_cpfw_background.png","./03_cpfw_bg_small.png")
    $bg-size(100%,100%)
    .outfit-list
      position absolute
      width 100%
      bottom 50px
      top 150px
      @media only screen and (max-width: 1080px)
        bottom 0
      $transition-title('outfitSlide')
      $screen-1080-top()
      text-align center
      margin 0 auto
      font-size 0
      .outfitTitle
        text-align center
        height auto
        @media only screen and (max-width: 1080px)
          margin-top  0
        img
          height auto
          $screen-1080-img-width()
      .outfit-img
        position absolute
        width 100%
        top 100px
        @media only screen and (max-width: 1080px)
          top 50px
        bottom 0
        left 0
        font-size 16px
        .leftWrap,.rightWrap
          position absolute
          top 0
          bottom 0
          width 41.66%
          background url("03_cpfw_jx.png") no-repeat
          $bg-size(100%,100%)
        .leftWrap
          left 0
          li
            p
              position absolute
              left 50%
              top 50%
              $translate(-50%,-50%)
              color #FFFFFF
          .leftTop
            position absolute
            @media only screen and (max-width: 1080px)
              font-size 12px
              top 0
              right 0
              img
                width 100%
            top -30px
            right 170px
          .leftBottom
            position absolute
            z-index 98
            @media only screen and (max-width: 1080px)
              font-size 12px
              bottom 0
              right 0
              img
                width 100%
            bottom -30px
            right -30px
        .rightWrap
          right 0
          li
            p
              position absolute
              left 50%
              top 50%
              $translate(-50%,-50%)
              color #FFFFFF
          .rightTop
            position absolute
            top -30px
            left  170px
            @media only screen and (max-width: 1080px)
              font-size 12px
              top 0
              left 0
              img
                width 100%
          .rightBottom
            position absolute
            z-index 100
            bottom -30px
            left -30px
            @media only screen and (max-width: 1080px)
              font-size 12px
              bottom 0
              left 0
              img
                width 100%
        .middleWrap
          position absolute
          line-height 0
          font-size 0
          left 50%
          top 50%
          z-index 99
          $translate(-50%,-50%)
          @media only screen and (max-width: 1080px)
            li
              width 100%
              img
                width 100%
              p
                font-size 16px !important
          li
            p
              position absolute
              left 50%
              top 50%
              font-size 24px
              line-height 1
              color #FFFFFF
              no-wrap()
              $translate(-50%,-50%)




  .cooperationPartner,.frameworkWrap  //业务模式  和 架构
    position relative
    $screen-1080-bg("./04_cpfw_background.png","./04_cpfw_bg_small.png")
    $bg-size(100%,100%)
    .partner-list,.framework-list
      position absolute
      width 100%
      bottom 0
      top 150px
      $screen-1080-top()
      text-align center
      margin 0 auto
      $transition-title('partnerSlide')
      $transition-title('frameworkSlide')
      .title
        text-align center
        height auto
        img
          height auto
          $screen-1080-img-width()
      .partner
        text-align center
        margin-top 60px
        img
          height auto
          @media only screen and (max-width: 1080px)
            width 85%




  .frameworkWrap  //机构
    $screen-1080-bg("./05_cpfw_background.png","./05_cpfw_bg_small.png")

  .perfectProWrap  //完美产品
    position relative
    $screen-1080-bg("./06_cpfw_background.png","./06_cpfw_bg_small.png")
    $bg-size cover
    background-position center
    .perfect-list
      position absolute
      width 100%
      bottom 0
      top 150px
      $screen-1080-top()
      text-align center
      margin 0 auto
      $transition-title('perfectSlide')
      .title
        text-align center
        height auto
        img
          height auto
          $screen-1080-img-width()
      .perfect
        width 1030px
        font-size 0
        @media only screen and (max-width: 1080px)
          width 100%
          margin-top 30px
        margin 0 auto
        li
          display inline-block
          text-align center
          @media only screen and (max-width: 1080px)
            width 30%
            margin 0 auto !important
            text-align center
          img
            @media only screen and (max-width: 1080px)
              width 100%
        li:nth-child(2){
          margin 0 50px
        }
        li:nth-child(5){
          margin 0 50px
        }




  .swiper-wrapper>div
        @media only screen and (min-width: 1080px)
          min-height 700px



</style>



